import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { Notif } from './notif.model';

@Injectable({providedIn: 'root'})
export class NotifService {

  // 
  private notifs : Notif[] = [];
  private notif : Notif;
  private notifS : Notif[] = [];
  private notifsUpdated = new Subject<Notif[]>();


  constructor(private http: HttpClient) { }
  
  getNotif() : any {
    this.http.get<{notifs: Notif[]}>('/api/notifs').subscribe((Data) => {
        this.notifs = Data.notifs;
        this.notif = this.notifs[0];
        
        this.notifsUpdated.next([...this.notifs]);
      
    });
  }
  getnotifsUpdatedListener() {
    return this.notifsUpdated.asObservable();
  }

  addNotif(
    id: string,
    userAccount:String,//id
    content: String,//
    ndate:String,//
    nstate:String,//"/"
    ntype:String,//"//"
  ){
    const notif: Notif = {
      _id: null,
      userAccount:userAccount,//id
        content: content,//
        ndate:ndate,//
        nstate:nstate,//"/"
        ntype:ntype,//"//"
    };
    this.http.post('/api/notifadd', notif).subscribe((responseData) =>{
        this.notifS.push(notif);
        this.notifsUpdated.next([...this.notifS]);
    });
  }

  
}