module.exports = "/* Set height of the grid so .sidenav can be 100% (adjust if needed) */\n.row.content {\n  height: 1500px; }\n/* Set gray background color and 100% height */\n.sidenav {\n  background-color: #f1f1f1;\n  height: 100%;\n  width: 200px; }\n/* On small screens, set height to 'auto' for sidenav and grid */\n@media screen and (max-width: 767px) {\n  .sidenav {\n    height: auto;\n    padding: 15px; }\n  .row.content {\n    height: auto; } }\n.section-title h2 {\n  font-size: 28px;\n  font-weight: 400;\n  margin-bottom: 20px;\n  padding-bottom: 20px;\n  position: relative;\n  text-align: center; }\n.section-title h4 {\n  text-align: center; }\n.section-title h2::before {\n  content: '';\n  position: absolute;\n  display: block;\n  width: 120px;\n  height: 1px;\n  background: #ddd;\n  bottom: 1px;\n  left: calc(50% - 60px); }\n.section-title h2::after {\n  content: '';\n  position: absolute;\n  display: block;\n  width: 40px;\n  height: 3px;\n  background: #02351F;\n  bottom: 0;\n  left: calc(50% - 20px); }\n.panel {\n  border: 1px solid #02351F;\n  border-radius: 0 !important;\n  transition: box-shadow 0.5s; }\n.panel:hover {\n  box-shadow: 5px 0px 40px rgba(253, 253, 253, 0.2); }\n.panel-footer .btn:hover {\n  border: 1px solid #02351F;\n  background-color: #fff !important;\n  color: #02351F; }\n.panel-heading {\n  color: #fff !important;\n  background-color: #02351F !important;\n  padding: 25px;\n  border-bottom: 1px solid transparent;\n  border-top-left-radius: 0px;\n  border-top-right-radius: 0px;\n  border-bottom-left-radius: 0px;\n  border-bottom-right-radius: 0px; }\n.panel-footer {\n  background-color: #02351F !important; }\n.panel-footer h3 {\n  font-size: 32px; }\n.panel-footer h4 {\n  color: white;\n  font-size: 14px; }\n.panel-footer .btn {\n  margin: 15px 0;\n  background-color: #02351F;\n  color: #fff; }\n\n/*# sourceMappingURL=data:application/json;base64, */"