import { Injectable } from '@angular/core';
import { HotelM } from './hm.model';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { School } from './school.model';

@Injectable({providedIn: 'root'})
export class SchoolService {

  private hotelMUpdated = new Subject<HotelM[]>();
  private datahotel  :any;
  // 
  private schools : School[] = [];
  private school : School;
  private schoolS : School[] = [];
  private schoolsUpdated = new Subject<School[]>();


  constructor(private http: HttpClient) { }
  
  getSchool() : any {
    this.http.get<{schools: School[]}>('/api/schools').subscribe((Data) => {
        //console.log(Data);
        this.schools = Data.schools;
        console.log(this.schools[0].userAccount);
        this.school = this.schools[0];
        //console.log(this.hotel.email);
        
        this.schoolsUpdated.next([...this.schools]);
      
    });
    // console.log("From  Hotel");
    // console.log(this.hotel)
    // return this.hotel;
  }
  getschoolsUpdatedListener() {
    return this.schoolsUpdated.asObservable();
  }

  addSchool(
    id: string,
    userAccount:String,
    state: String, //offer
    univName: String,//
    schoolName: String,//
    majorName: String,//
    ddl1: String,//DDL 1
    ddl2: String,//DDL 2
    ddl3: String,//DDL 3
    interview: String,// -
    videoEssay: String, // videoEssay()
    link: String, //
    applyAccount:String,//
    applyPassword:String,//
    recommNumber:String,//
    other:String,
    level:String,// 
  ){
    const school: School = {
      _id: null,
      userAccount:userAccount,//id
        state: state, //offer
        univName: univName,//
        schoolName: schoolName,//
        majorName: majorName,//
        ddl1: ddl1,//DDL 1
        ddl2: ddl2,//DDL 2
        ddl3: ddl3,//DDL 3
        interview: interview,// -
        videoEssay: videoEssay, // videoEssay()
        link: link, //
        applyAccount:applyAccount,//
        applyPassword:applyPassword,//
        recommNumber:recommNumber,//
        other:other,
        level:level,// 
    };
    this.http.post('/api/schooladd', school).subscribe((responseData) =>{
        this.schoolS.push(school);
        this.schoolsUpdated.next([...this.schoolS]);
    });
  }

  
}