import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { Education } from './education.model';

@Injectable({providedIn: 'root'})
export class EducationService {

  // 
  private educations : Education[] = [];
  private education : Education;
  private educationS : Education[] = [];
  private educationsUpdated = new Subject<Education[]>();


  constructor(private http: HttpClient) { }
  
  getEducation() : any {
    this.http.get<{educations: Education[]}>('/api/educations').subscribe((Data) => {
        this.educations = Data.educations;
        this.education = this.educations[0];
        
        this.educationsUpdated.next([...this.educations]);
      
    });
  }
  getrecommendersUpdatedListener() {
    return this.educationsUpdated.asObservable();
  }

  addEducation(
    id: string,
    userAccount:String,//id
    type: String,//
    educationName: String,
    educationLocation:String,
    educationStart:String,//
    educationEnd:String,//
    major: String,
    Degree:String,
    GPA:String,
    MajorGPA:String,//GPA
    SecondDegree:String,//
    SecondGPA:String,// GPA
    other:String,
  ){
    const education: Education = {
      _id: null,
      userAccount:userAccount,//id
      type: type,//
      educationName: educationName,
      educationLocation:educationLocation,
      educationStart:educationStart,//
      educationEnd:educationEnd,//
      major: major,
      Degree:Degree,
      GPA:GPA,
      MajorGPA:MajorGPA,//GPA
      SecondDegree:SecondDegree,//
      SecondGPA:SecondGPA,// GPA
      other:other,
    };
    this.http.post('/api/educationadd', education).subscribe((responseData) =>{
        this.educationS.push(education);
        this.educationsUpdated.next([...this.educationS]);
    });
  }

  
}