import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { Statement } from './statement.model';

@Injectable({providedIn: 'root'})
export class StatementService {

  // 
  private statements : Statement[] = [];
  private statement : Statement;
  private statementS : Statement[] = [];
  private statementsUpdated = new Subject<Statement[]>();


  constructor(private http: HttpClient) { }
  
  getStatement() : any {
    this.http.get<{statements: Statement[]}>('/api/statements').subscribe((Data) => {
        this.statements = Data.statements;
        this.statement = this.statements[0];
        
        this.statementsUpdated.next([...this.statements]);
      
    });
  }
  getstatementsUpdatedListener() {
    return this.statementsUpdated.asObservable();
  }

  addStatement(
    id: string,
    schoolID:String, //id
    question: String,//
    stype:String,//(PS/SOP/Essay)
    words:String,//
  ){
    const statement: Statement = {
      _id: null,
      schoolID:schoolID, //id
        question: question,//
        stype:stype,//(PS/SOP/Essay)
        words:words,//
    };
    this.http.post('/api/statementadd', statement).subscribe((responseData) =>{
        this.statementS.push(statement);
        this.statementsUpdated.next([...this.statementS]);
    });
  }

  
}