import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { Recommender } from './recom.model';

@Injectable({providedIn: 'root'})
export class RecommenderService {

  // 
  private recommenders : Recommender[] = [];
  private recommender : Recommender;
  private recommenderS : Recommender[] = [];
  private recommendersUpdated = new Subject<Recommender[]>();


  constructor(private http: HttpClient) { }
  
  getRecommender() : any {
    this.http.get<{recommenders: Recommender[]}>('/api/recommenders').subscribe((Data) => {
        //console.log(Data);
        this.recommenders = Data.recommenders;
        //console.log(this.recommenders[0].userAccount);
        this.recommender = this.recommenders[0];
        //console.log(this.hotel.email);
        
        this.recommendersUpdated.next([...this.recommenders]);
      
    });
    // console.log("From  Hotel");
    // console.log(this.hotel)
    // return this.hotel;
  }
  getrecommendersUpdatedListener() {
    return this.recommendersUpdated.asObservable();
  }

  addRecommender(
    id: string,
    userAccount:String, //id
    firstName: String,//
    lastName: String,
    email: String,//email
    phone: String,//
    title: String, // 
    location: String,//
    gender: String,//
    education:String, //
    organization:String, //
    duration:String,//
    position:String, //
    relation:String,//
    other:String,//
  ){
    const recommender: Recommender = {
      _id: null,
      userAccount:userAccount,//id
      firstName: firstName,//
      lastName: lastName,
      email: email,//email
      phone: phone,//
      title: title, // 
      location: location,//
      gender: gender,//
      education:education, //
      organization:organization, //
      duration:duration,//
      position:position, //
      relation:relation,//
      other:other,//
    };
    this.http.post('/api/recommenderadd', recommender).subscribe((responseData) =>{
        this.recommenderS.push(recommender);
        this.recommendersUpdated.next([...this.recommenderS]);
    });
  }

  
}