import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { Blog } from './blog.model';

@Injectable({providedIn: 'root'})
export class BlogService {

  // 
  private blogs : Blog[] = [];
  private blog : Blog;
  private blogS : Blog[] = [];
  private blogsUpdated = new Subject<Blog[]>();


  constructor(private http: HttpClient) { }
  
  getActivity() : any {
    this.http.get<{blogs: Blog[]}>('/api/blogs').subscribe((Data) => {
        this.blogs = Data.blogs;
        this.blog = this.blogs[0];
        
        this.blogsUpdated.next([...this.blogs]);
      
    });
  }
  getblogsUpdatedListener() {
    return this.blogsUpdated.asObservable();
  }

  addBlog(
    id: string,
    blogTitle:String,//
    blogSubtitle: String,//
    blogTime:String,//
    blogPicture:String,//
    blogLink: String,//link
    countNumber:String,
  ){
    const blog: Blog = {
      _id: null,
      blogTitle:blogTitle,//
      blogSubtitle: blogSubtitle,//
      blogTime:blogTime,//
      blogPicture:blogPicture,//
      blogLink: blogLink,//link
      countNumber:countNumber,
    };
    this.http.post('/api/blogadd', blog).subscribe((responseData) =>{
        this.blogS.push(blog);
        this.blogsUpdated.next([...this.blogS]);
    });
  }

  
}