import { Component, Input, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { StService } from '../st.service';
import {Student} from '../st.model';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute,  NavigationExtras,Router } from '@angular/router';
import {School} from '../school.model';
import {Recomletter} from '../recletter.model';
import {Statement} from '../statement.model';

@Component({
  selector: 'app-studentschool',
  templateUrl: './studentschool.component.html',
  styleUrls: ['./studentschool.component.scss']
})
export class StudentschoolComponent implements OnInit {
  students: Student[] = [];
  student : Student;

  studentUserAcc : any;
  studentID : any;
  //school 
  schools: School[] = [];
  school: School; //
  schoolID  : any;//school ID
  
    //school 
  
    state: String; //offer
    univName: String;//
    schoolName: String;//
    majorName: String;//
    ddl1: String;//DDL 1
    ddl2: String;//DDL 2
    ddl3: String;//DDL 3
    interview: String;// -
    videoEssay: String;// videoEssay()
    link: String;//
    applyAccount:String;//
    applyPassword:String;//
    recommNumber:String;//
    other:String;  
  //
  recomletters : Recomletter[] = [];
  recomletter : Recomletter;
  //
  statements : Statement[] = [];

  private stSub: Subscription;

  constructor(
    public stService: StService,
    private http: HttpClient,
    private route: ActivatedRoute, 
    private router: Router,
    ) { 
      this.route.queryParams.subscribe(params => {
        this.studentUserAcc = params["studentUserAcc"];
        this.studentID= params["studentID"];
        this.schoolID= params["schoolID"];
       });
    }
    

  ngOnInit() {


    this.http.get<{students: Student[]}>('/api/students').subscribe((Data) => {
        
        this.students = Data.students;
        for(let h of this.students){
            if(h.userAccount===this.studentUserAcc){
              this.student = h;
              console.log(""+this.student.lastName);
            }
        }
        // this.studentID = this.student._id;
        console.log("id"+this.studentID);

    });

     // 
    this.http.get<{schools: School[]}>('/api/schools').subscribe((Data) => {
          //console.log(orderData);
          this.schools = Data.schools;
          for(let s of this.schools){
            if(s._id===this.schoolID){
              //console.log("");
              this.school  = s;
              this.state = this.school.state; //offer
              this.univName= this.school.univName;//
              this.schoolName= this.school.schoolName;//
              this.majorName= this.school.majorName;//
              this.ddl1= this.school.ddl1;//DDL 1
              this.ddl2= this.school.ddl2;//DDL 2
              this.ddl3= this.school.ddl3//DDL 3
              this.interview= this.school.interview;// -
              this.videoEssay= this.school.videoEssay;// videoEssay()
              this.link= this.school.link//
              this.applyAccount= this.school.applyAccount;//
              this.applyPassword= this.school.applyPassword;//
              this.recommNumber= this.school.recommNumber;//
              this.other= this.school.other;
            }
        }
      });
    //   
    this.http.get<{recomletters: Recomletter[]}>('/api/schoolrecomletterlist/' + this.schoolID).subscribe((Data) => {
      //console.log(Data);
      this.recomletters = Data.recomletters;
    }); 
    
    //   
    this.http.get<{statements: Statement[]}>('/api/statementdetail/' + this.schoolID).subscribe((Data) => {
      //console.log(Data);
      this.statements = Data.statements;
    }); 
 


    this.stSub = this.stService.getstudentsUpdatedListener().subscribe((students: Student[]) => {
    this.students = students;
    });
  }

  //direct to the student main page
  studentmain(student) {
    const navigationExtras: NavigationExtras = {
      queryParams: {
       "studentUserAcc" : student.userAccount,
       "studentID" : this.studentID,
      }
    };
    this.router.navigate(['/studentmain'], navigationExtras);
  }
  
  //direct to the  page
  addRecom(student) {
    const navigationExtras: NavigationExtras = {
      queryParams: {
       "studentUserAcc" : student.userAccount,
       "studentID" : student._id,
      }
    };
    this.router.navigate(['/studentaddrecomm'], navigationExtras);
  }
  //direct to the  page
  info(student) {
    const navigationExtras: NavigationExtras = {
      queryParams: {
       "studentUserAcc" : student.userAccount,
       "studentID" : student._id,
      }
    };
    this.router.navigate(['/studentinfo'], navigationExtras);
  }

  //direct to the  page
  rec(student) {
    const navigationExtras: NavigationExtras = {
      queryParams: {
       "studentUserAcc" : student.userAccount,
       "studentID" : student._id,
      }
    };
    this.router.navigate(['/studentrec'], navigationExtras);
  }

  //direct to the  page
  resume(student) {
    const navigationExtras: NavigationExtras = {
      queryParams: {
       "studentUserAcc" : student.userAccount,
       "studentID" : this.studentID,
      }
    };
    this.router.navigate(['/studentresume'], navigationExtras);
  }

  ngOnDestroy() {
    this.stSub.unsubscribe();
  }
}
